#root {
  height: 100%;
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  line-height: normal;
}

body {
  background-color: #1d1d20;
  color: #e5e7ea;
}

h2 {
  font-size: 1.5em;
  margin-bottom: 6px;
}

p {
  font-size: 1em;
  margin-bottom: 6px;
}

.a {
  transition: color 100ms linear;
  color: rgba(127, 171, 247, 0.85);
}
.a:hover, .a:active {
  color: #7fabf7;
}
.a:visited {
  color: rgba(127, 171, 247, 0.5);
}

.separator-v {
  padding: 0 6px;
}
.separator-v div {
  height: 100%;
  width: 2px;
  background-color: rgba(127, 171, 247, 0.25);
}